import React from 'react';
import { Link } from 'react-router-dom';

import DashboardCard from 'src/components/DashboardCard';
import FilterBox from 'src/components/FilterBox';

const jobCategory = [
  { position: 'Graphic designer', count: 155 },
  { position: 'Engineering projects', count: 514 },
  { position: 'Mainframe projects', count: 554 },
  { position: 'Legal projects', count: 457 },
  { position: 'IT projects', count: 254 },
  { position: 'PSU projects', count: 1054 },
  { position: 'Government projects', count: 1284 },
];

const JobCategory = () => {
  return (
    <DashboardCard title="Projects by category" className="mb-5">
      {Array.isArray(jobCategory) &&
        jobCategory.map((item, i) => <FilterBox key={i} {...item} />)}
      {jobCategory.length >= 7 && (
        <Link to="#" className="text-primary ml-2">
          View all categories{' '}
        </Link>
      )}
    </DashboardCard>
  );
};

export default JobCategory;
